.sidebar {
    width: 80px;
    background-color: #1c1c1c;
    padding: 20px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.sidebar-item {
    padding: 20px;
    cursor: pointer;
}

.sidebar-item img {
    width: 24px;
    height: 24px;
    filter: invert(1); /* Makes icons white */
    transition: transform 0.3s ease;
}

.sidebar-item:hover img {
    transform: scale(1.2);
}
