.onebox-container {
    display: flex;
    height: 100vh;
}

.main-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    background-color: #121212;
}

.top-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    background-color: #1f1f1f;
    border-bottom: 1px solid #333;
}

.workspace-title {
    font-size: 1.5rem;
    color: #fff;
}

.workspace-name {
    font-size: 1rem;
    color: #bbb;
}

.content {
    display: flex;
    height: 100%;
}

.no-email-selected {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #fff;
    text-align: center;
    padding: 20px;
}

.empty-inbox-image {
    width: 200px;
    margin-bottom: 20px;
}

.dark {
    background-color: #000;
    color: #fff;
}

.light {
    background-color: #f4f4f4;
    color: #000;
}
